<script>
    // import Login from '@/views/Login'
    import Clipboard from 'clipboard';
    import {myAlert} from '@/utils/utils';
    export default {
        name: 'Header',
        components: {
            // Login
        },
        data() {
            return {
                show: false,
                isShowLogin: false,
                isShowMainMenu: false,
                isShowAbout: false,
                activeIndex: null,
                dict:
                    [
                        {
                            path: '/home',
                            discp: '主页🏠'
                        },
                        {
                            path: '/life',
                            discp: '我的画🎨'
                        },
                        {
                            path: '/Guitar',
                            discp: '吉他🎸'
                        },
                        {
                            path: '/support',
                            discp: '支持我💰'
                        },
                        {
                            path: '/about',
                            discp: '关于❔'
                        },
                    ]
            }
        },
        created() {
            this.isShowMainMenu = this._isMobile() ? false : true
        },
        methods: {
            copy() {
                var clipboard = new Clipboard('.icon-copy')
                clipboard.on('success', e => {
                    myAlert('复制成功')
                    clipboard.destroy()// 释放内存
                })
                clipboard.on('error', e => {
                    myAlert('不支持复制,该浏览器不支持自动复制')
                    clipboard.destroy()// 释放内存
                })
            },
            bgcChange(index) {
                this.activeIndex = index
                this.isShowMainMenu = this._isMobile() ? false : true
            },
            toggle() {
                this.show = !this.show;
            },
            handleIsShowMainMenu(index) {
                this.isShowMainMenu = !this.isShowMainMenu
                this.isShowAbout = false
            },
            handleIsShowAbout(index) {
                this.isShowAbout = !this.isShowAbout
                this.isShowMainMenu = false
            },
            changeIsShowLogin() {
                this.isShowLogin = true;
            },
            queryFunction() {
                this.isShowLogin = false;
            },
            _isMobile() {
                let flag = navigator.userAgent.match(
                    /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
                );
                if (flag == null) {
                    return false;
                } else {
                    return true;
                }
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @import '../assets/css/variable';

    .item {
        //改鼠标为小手
        cursor: pointer;
    }

    .bg {
        background-color: $background-color4;
    }

    .header {
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid $border-color;
        background: $background-color;
        z-index: 999;

        ul {
            display: inline;

            li {
                width: 85px;
                line-height: 39px;
                float: left;
                text-align: center;
                font-size: $font_medium;
                color: $font-color;
            }
        }

        .header-right {
            margin-right: 10px;
        }

        .phone {
            display: none;
        }
    }

    @media screen and (max-width: 1000px) {
        .header {
            background-color: $background-color;
        }
        .bg {
            background-color: $background-color2;
        }
        .header-left {
            display: inline;
            width: 100px;
            display: flex;
        }
        a {
            color: $font-color2;

            &:hover {
                color: $font-active-color-theme1;
            }
        }
        .home-right {
            z-index: auto;
            width: 60%;
            height: 100%;
            float: left;
            padding: 15px;
            box-sizing: border-box;
            background-color: $background-color1;
            border-bottom-left-radius: 10px;
            position: fixed;
            right: 0;
            top: 40px;
            color: $font-color;

            p:first-of-type {
                margin-top: 20px;
            }
        }
        .pc1 {
            position: absolute;
            top: 40px;
            left: 0;
            height: 1110px !important;
            z-index: auto;
            width: 60%;

            background-color: $background-color1;

            ul {
                display: block;

                li {
                    width: 100%;
                    line-height: 50px;
                    border-bottom: 1px solid $font-color;
                }
            }
        }
        .phone {
            display: inline !important;
            /*height: 40px;*/
        }
    }
</style>
